
import Panel from 'vux/src/components/panel/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import Badge from '../components/Badge'
let currentTab = 'all'

export default {
name: 'TopicList',
components: {
Panel,
LoadMore,
Tab,
TabItem,
Badge
},
data () {
const categories = [
{name: '全部', key: 'all', queryData: 'all'},
{name: '精华', key: 'good', queryData: 'good'},
{name: '分享', key: 'share', queryData: 'share'},
{name: '招聘', key: 'job', queryData: 'job'},
{name: '问答', key: 'ask', queryData: 'ask'},
{name: '测试', key: 'dev', queryData: 'dev'}
]
return {
type: '1',
loadTip: 'loading',
categories: categories
}
},
computed: {
currentTab: function () {
return this.$store.getters.currentTab
}
},
methods: {
changeTab (queryData) {
currentTab = queryData
this.$store.dispatch('change_tab', currentTab)
this.$store.dispatch('refreshTopiclistData')
},
refresh (done) {
setTimeout(() => {
this.$store.dispatch('change_tab', currentTab)
this.$store.dispatch('refreshTopiclistData')
done()
}, 1000)
},
infinite (done) {
setTimeout(() => {
this.$store.dispatch('change_tab', currentTab)
this.$store.dispatch('loadMoreTopicList')
done()
}, 1000)
},
isShowBadge (item) {
return item.tab || item.top || item.good
}
}
}



