
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Group from 'vux/src/components/group/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default{
data () {
return {
accessToken: ''
}
},
components: {
XButton,
XInput,
Flexbox,
Group,
FlexboxItem
},
methods: {
onLogin () {
let accessToken = this.accessToken
this.$store.dispatch('login', accessToken)
if (this.$store.getters.loginInfo) {
this.$vux.toast.show({
text: '登录成功'
})
this.$router.push('/')
} else {
this.$vux.toast.show({
text: '登录失败',
type: 'warn'
})
}
},
setLoginInfoDetail (loginname) {
this.axios.get('https://cnodejs.org/api/v1/user/' + loginname)
.then(result => {
this.$store.commit('SET_LOGIN_INFO', result.data.data)
})
}
}
}



